html, body {
	margin:0;
	padding:0;
	height:100%;
	width:100%;

	background:#272727;
	color:white;
	font-family: 'Glegoo', serif;

}


.homeImage {
    -webkit-filter: brightness(0.8);
            filter: brightness(0.8);
    position:fixed !important;
}

.popular_display {
	top: 250px;
    height: calc(100% - 260px);
    width: calc(100%);
    position: absolute;
    overflow: hidden;
	
}

.popular_display h1 {
	padding-left:25px;
	text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
	padding-left: 25px;
    padding-right: 25px;
    text-shadow: -1px -1px black, 0 1px black, 1px 0 black, 0 -1px black;
    display: inline-block;
    background: #000000b3;
    margin: 0;
}

.popular_outer {
	position: relative;
	
	height:calc(50% - 107px);
	width:100%;
	margin:25px 0 25px 0;
}

.popular {
	position: relative;
	
	height:100%;

	display:-ms-flexbox;

	display:flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: start;
	    justify-content: flex-start;

	
	-webkit-box-sizing: border-box;

	
	        box-sizing: border-box;

}

.box {
	position: relative;
	height:100%;
	width:auto;
	margin-right:5px;
	cursor: pointer;

}

.box:hover {
	-webkit-filter:brightness(1.2);
	        filter:brightness(1.2);
}

.box:hover .title {
	color:#45e245;
	background: rgba(0,0,0,0.8);
}

.box img {
	height:100%;
	width:auto;
}

.box .title {
	position: absolute;
	bottom:0;
	min-height:35px;
	background:rgba(0,0,0,0.4);
	color:white;
	width:100%;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	padding-left:15px;
	padding-top:5px;
	line-height:25px;
	text-transform: uppercase;
	font-size:20px;
	max-height: 100%;
    overflow: hidden;
}


.arrows {
	position:absolute;
	height:80px;
	top:calc(50% - 25px);
	z-index: 5;
	cursor: pointer;

	color:white;
	font-size:50px;
	opacity: 0.7;
	text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}

.arrows:hover {
	color:#45e245;
	opacity: 1;
}
.left {
	left:10px;	
}
.right {
	right:10px;
}






/* SEARCH AREA */
.searchArea {
	position: relative;
	width:750px;
	max-width: 100%;
	margin:auto;
	top:150px;
	height:80px;
}




.searchArea .search {
	left:75px;
	position: absolute;
	height:100%;
	width:calc(100% - 150px);
	font-size:40px;
	padding: 0;

	border: 1.5px #1c1313 solid;
    border-right: none;
    border-left: none;
   
    margin: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    text-indent: 15px;

    opacity: 0.6;
}

.searchArea .search:focus {outline:none;}

.searchArea .submit, .searchArea .home {
	right:0;
	position: absolute;
	height:100%;
	width:75px;
	background: #1f1f1f;
	cursor: pointer;
	border: 1.5px #1c1313 solid;
    border-left: none;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-size: 60px;
    line-height: 80px;
    text-align: center;
    overflow: hidden;
}

.searchArea .home {
	left: 0;
    
    border-right: none;
    border-left: solid 1.5px black;
}

.searchArea .submit:hover, .searchArea .home:hover {
	color:#45e245;
}



.infoArea {
	position: relative;
	width:750px;
	max-width: 100%;
	margin:auto;
	top: 150px;
    height: 40px;
    background: #00000091;
    text-align: center;
    line-height: 40px;
}





/* SEARCH RESULTS */

img.background {
	width:100%;
	height:auto;
	position: fixed;
}
.results {
	position: absolute;
    top: 30%;
    right: 0;
    background: rgba(0,0,0,0.7);
    width: 450px;
    padding: 25px;

    max-width: calc(100% - 50px);
}
.row {
	position: relative;
	padding-bottom: 5px;
}


.row .track {
	width: calc(100% - 30px);
    display: inline-block;
}

#loading_animation {
	display:none;
	width:100%;
}


.results .buttons {
	width:100%;
	height: 40px;
    margin-bottom: 20px;
}

.add, .update, .buttons .home {
	padding:5px;
	border:solid 1px white;
	float:right;
	background: #262626;
	cursor: pointer;
	margin-left:3px;
}

.update:hover, .add:hover, .buttons .home:hover {
	background: white;
	color:#262626;
}

.add {
	background: #27b327;
}

.buttons .home {
	background:#1489b0;
	float:left;
	height: 28.8px;
    font-size: 24px;
    line-height: 28px;
}

.grayed {
	background: gray;
	cursor: not-allowed;
}

.grayed:hover {
	background: gray;
	color:white;
}




.playlists, #urlArea {
	position: absolute;
    top: calc(30% - 55px);
    right: 0;
    background: rgba(0,0,0,0.7);
    width: 500px;
    height: 40px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;

    max-width: 100%;
}
#urlArea {
	height:80px;
	top:calc(30% - 90px);
	line-height: 80px;
}

#urlArea a {
	color:white;
	text-decoration: underline;
	text-align: center;
    width: 100%;
}

.playlists .current {
	height: 100%;
    background: #140d12;
    -ms-flex-positive: 1;
        flex-grow: 1;
    text-align: center;
    line-height: 40px;
    cursor: pointer;
}

.playlists .dropdown {
	display:none;
	top: 40px;
    position: absolute;
    left: 0;
    width: 500px;
    background: black;
}

.playlists .dropdown .item {
	padding:3px;
	text-indent: 15px;
	cursor:pointer;
}

.playlists .dropdown .item:nth-child(odd) {
	background: #8397d433;
}

.playlists .item:hover, .playlists .current:hover {
	background: white !important;
    color: #262626;
}


.playlists .dropdown .selected {
	background:yellow;
}

.playlists .search {
	height: 100%;
    width: 300px;
    border: 0;
    padding: 0;
    margin: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background: #f1f1f1;
    font-size: 16px;
    text-indent: 10px;
    font: inherit;
}

.playlists .hidden {
	display:none;
}

.playlists .search:focus {outline:none;}

.playlists .submit {
    height: 100%;
    background: #58b078;
    width: 50px;
    cursor: pointer;
}



.results .row .found, .results .row .missing {
	width: 15px;
    height: 15px;
    margin-left: 5px;
    background: lightgreen;
    display: inline-block;
    float:right;
    border-radius:50%;
}

.results .row .found:hover {
	-webkit-filter:brightness(0.8);
	        filter:brightness(0.8);
}

.results .row .missing {background: red;}




/* 700 - 1367 */
@media all and (min-width: 700px) and (max-width: 1367px) {

	.searchArea, .infoArea {
		top:0;
	}
}




/* LESS THAN 700 */
@media all and (max-width: 700px) {
    img.background, .searchArea, #playlistArea, .results, #urlArea {
    	position: relative;
    	top:0;
    	
    }


    .searchArea .search {
    	font-size: 18px;
    	background: #ececec;
    	opacity: 1;
    }

    .homeImage {display: none;}

    .popular_display {
    	position: relative;
    	top:0;
    	height:auto;
    }

    .popular_outer {
    	height:200px;
    	overflow-x:scroll;
    }


    .popular_outer .arrows {display:none;}


    .infoArea {
    	top:0;
    	height:auto;
    	margin-bottom:15px;
    }


    .results {
    	width:100%;
    }

}


